import axios from "axios"

const URL = "https://api.simuladordecreditohipotecarioexpress.com/server/"

export default {
    post(endpoint, data) {

		var formDa = new FormData();
		for (var key in data) {
			formDa.append(key, data[key]);
        }
        
        return axios.post(URL + endpoint, formDa).then(response => {
            return response
        })
    }
}