import store from "../store";


export default (state) => {
    const data = {
        nombre: "santander",
        factorSeguroDeVida: 0.0006,
        iva: 0,

        productos: [{
            nombre: "Comprar mi Casa",
            porcentajeMaximoDeCredito: 0.9,
            plazosFijos: [true, true, true],
            plazosCrecientes: [false, true, true],
            // mayor
            factoresAmortizacionFijosMa80: [0.5951224242, 0.2951463973, 0.1492409756],
            factoresAmortizacionCrecientesMa80: [0, 0.1333333333, 0.0782439024],
            //menor 
            factoresAmortizacionFijosMe80: [0.6253284924, 0.3134460936, 0.1617037954],
            factoresAmortizacionCrecientesMe80: [0, 0.1474453613, 0.0884847758],
            tasaMa80: [0.0990, 0.0990, 0.1025],
            tasaMe80: [0.0959, 0.0959, 0.0990],
            factorSeguroDeDanos: 0.000259,
            comisionAdministracion: 290
        }]
    }

    var productoAsignado = () => {
        return data.productos.filter(producto => {
            return producto.nombre == state.productoSeleccionado;
        })[0]; // devolver el primer objeto para no obtener un array
    }

    var porcentajeDeCredito = () => {

        return (
            (store.getters._montoDeCredito * 100) /
            state.valorDelInmueble /
            100
        ) || 0;

    }

    var tasa = () => {
        if (store.getters._montoDeCredito > (state.valorDelInmueble * .8)) {
            return productoAsignado().tasaMa80[state.plazoSeleccionado];
        } else if (store.getters._montoDeCredito <= (state.valorDelInmueble * .8)) {
            return productoAsignado().tasaMe80[state.plazoSeleccionado];
        }
    }

    var factorAmortizacion = () => {
        if (store.getters._montoDeCredito > (state.valorDelInmueble * .8)) {
            var tipoDeFactores;
            if (state.tipoDePago == 0) tipoDeFactores = "factoresAmortizacionFijosMa80";
            if (state.tipoDePago == 1)
                tipoDeFactores = "factoresAmortizacionCrecientesMa80";
            return productoAsignado()[tipoDeFactores][state.plazoSeleccionado];
        } else if (store.getters._montoDeCredito <= (state.valorDelInmueble * .8)) {
            var tipoDeFactores;
            if (state.tipoDePago == 0) tipoDeFactores = "factoresAmortizacionFijosMe80";
            if (state.tipoDePago == 1)
                tipoDeFactores = "factoresAmortizacionCrecientesMe80";
            return productoAsignado()[tipoDeFactores][state.plazoSeleccionado];
        }

    }


    var factorSeguroDeVida = () => {
        return data.factorSeguroDeVida
    }

    var factorSeguroDeDanos = () => {
        return productoAsignado().factorSeguroDeDanos;
    }

    var comisionAdministracion = () => {
        return productoAsignado().comisionAdministracion;
    }

    var primaUnica = () => {
        return store.getters._montoDeCredito * 0.03154;
    }

    var montoDeCreditoTotal = () => {
        console.log(store.getters._montoDeCredito, primaUnica(), tasa());

        if (store.getters._montoDeCredito < (state.valorDelInmueble * 0.78)) {
            console.log(store.getters._montoDeCredito + primaUnica())
            return store.getters._montoDeCredito + primaUnica();
        } else {
            console.log("no suma",store.getters._montoDeCredito);

            return store.getters._montoDeCredito
        }
    }

    var interes = () => {
        // return (store.getters._montoDeCredito * tasa()) / 12;
        return (montoDeCreditoTotal() * tasa()) / 12;
    }

    var amortizacion = () => {
        return interes() * factorAmortizacion();
    }

    var pagoMensual = () => {
        return interes() + amortizacion()
    }


    var seguroDeVida = () => {
        if (store.getters._montoDeCredito < (state.valorDelInmueble * 0.78)) {
            return 0
        } else {
            return store.getters._montoDeCredito * factorSeguroDeVida()
        }
    }

    var seguroDeDanos = () => {
        return state.valorDelInmueble * factorSeguroDeDanos();
    }

    var pagoMensual = () => {
        return parseFloat((interes() + amortizacion()).toFixed(2)) + seguroDeDanos() + seguroDeVida() + comisionAdministracion();
    }

    var ingresosRequeridos = () => {
        if (state.tipoDeIngresoSeleccionado == "Asalariado") {
            return pagoMensual() * 2;
        } else {
            return pagoMensual() * 2.78;
        }
    }

    var creditoInicial = () => {
        if (state.tipoDeIngresoSeleccionado == "Asalariado" && (state.tipoDeApoyoSeleccionado == "cofinavit" || state.tipoDeApoyoSeleccionado == "infonavit")) {
            return state.valorDelInmueble * .95;
        } else if (state.tipoDeIngresoSeleccionado == "Asalariado") {
            return state.valorDelInmueble * .90;
        } else {
            return state.valorDelInmueble * .90;
        }
    }

    var disponibilidad = () => {

        var credito = false;
        // console.log(store.getters._montoDeCredito, creditoInicial());

        if (store.getters._montoDeCredito <= creditoInicial()) {
            credito = true
        }
        // console.log("credito:", credito)
        var pago = state.tipoDePago;
        var plazo = false;
        if (pago == 0) {
            plazo = productoAsignado().plazosFijos[state.plazoSeleccionado]
        } else if (pago == 1) {
            plazo = productoAsignado().plazosCrecientes[state.plazoSeleccionado]
        }
        // console.log("plazo", plazo)

        // console.log("credito:", credito)
        // console.log(credito);
        // console.log(plazo, credito);
        if (plazo && credito) {
            return true
        } else {
            return false
        }
    }


    return {
        nombre: data.nombre,
        interes: interes(),
        amortizacion: amortizacion(),
        seguroDeVida: seguroDeVida(),
        seguroDeDanos: seguroDeDanos(),
        pagoMensual: pagoMensual(),
        ingresosRequeridos: ingresosRequeridos(),
        tasa: tasa(),
        creditoInicial: creditoInicial(),
        porcentajeDeCredito: porcentajeDeCredito(),
        disponibilidad: disponibilidad(),
        primaUnica: primaUnica()

    }

}