import store from "../store";

export default (state) => {

	const data = {
		nombre: "banamex",
		factorSeguroDeVida: 0.0005,
		iva: 0,
		// plazo 10 15 20

		productos: [{
			nombre: "Comprar mi Casa",
			plazosFijos: [true, true, true],
			porcentajeMaximoDeCredito: 0.85, // asalariados
			porcentajeMaximoDeCreditoNoAsalariados: 0.8, // otros
			pagoFijo: {
				mayorDosMillones: {
					mayorOchenta: {
						factorInteres: [0.008041668, 0.008041668, 0.008041668],
						factorAmortizacion: [0.620311, 0.31067368, 0.1713991],
						seguroDanos: 0.0003
					},
					menorOchenta: {
						factorInteres: [0.008041668, 0.008041668, 0.008041668],
						factorAmortizacion: [0.620311, 0.31067373, 0.1713991],
						seguroDanos: 0.0003
					}
				},
				menorDosMillones: {
					mayorOchenta: {
						factorInteres: [0.00825, 0.00825, 0.008041668],
						factorAmortizacion: [0.595151515, 0.295757576, 0.1713991],
						seguroDanos: 0.0003
					},
					menorOchenta: {
						factorInteres: [0.008249999, 0.00825, 0.00825],
						factorAmortizacion: [0.595152, 0.29575758, 0.16242425],
						seguroDanos: 0.0003
					}
				}
			},
			pagoCreciente: {
				mayorDosMillones: {
					mayorOchenta: {
						factorInteres: [0, 0, 0.008041668],
						factorAmortizacion: [0, 0, 0.05823845],
						seguroDanos: 0.0003
					},
					menorOchenta: {
						factorInteres: [0, 0, 0.008041668],
						factorAmortizacion: [0, 0, 0.05823845],
						seguroDanos: 0.0003
					}
				},
				menorDosMillones: {
					mayorOchenta: {
						factorInteres: [0, 0, 0.000825],
						factorAmortizacion: [0, 0, 0.0315151516],
						seguroDanos: 0.0003
					},
					menorOchenta: {
						factorInteres: [0, 0, 0.00825],
						factorAmortizacion: [0, 0, 0.031516],
						seguroDanos: 0.0003
					}
				}
			},

			plazosCrecientes: [false, false, true],
			factoresFijos: [0.620310784, 0.310673497, 0.171398894],
			factoresCrecientes: [0, 0, 0.032331604],
			tasa: [0.099, 0.0965],
			comisionAdministracion: 0
		}]

	}

	var productoAsignado = () => {
		return data.productos.filter(producto => {
			return producto.nombre == state.productoSeleccionado;
		})[0]; // devolver el primer objeto para no obtener un array
	}

	var tasa = () => {
		if (store.getters._montoDeCredito >= 2000000) return productoAsignado().tasa[1];
		if (store.getters._montoDeCredito < 2000000) return productoAsignado().tasa[0];
		if (
			store.getters._montoDeCredito < 2000000 &&
			state.valorDelInmueble >= 6000000
		)
			return productoAsignado().tasa[1];
	};

	var porcentajeDeCredito = () => {

		return (
			(store.getters._montoDeCredito * 100) /
			state.valorDelInmueble /
			100
		) || 0;

	}

	var factorAmortizacion = () => {
		// factor de amortizacion

		var tipoDePagoSeleccionado;
		if (state.tipoDePago == 0) tipoDePagoSeleccionado = "pagoFijo";
		if (state.tipoDePago == 1)
			tipoDePagoSeleccionado = "pagoCreciente";
		var dosMillones;
		if (store.getters._montoDeCredito >= 2000000) dosMillones = "mayorDosMillones";
		if (store.getters._montoDeCredito < 2000000) dosMillones = "menorDosMillones";
		var ochenta;
		if ((porcentajeDeCredito() * 100) >= 80) ochenta = "mayorOchenta";
		if ((porcentajeDeCredito() * 100) < 80) ochenta = "menorOchenta";

		return productoAsignado()[tipoDePagoSeleccionado][dosMillones][
			ochenta
		]["factorAmortizacion"][state.plazoSeleccionado];
	}

	var factorSeguroDeVida = () => {
		return data.factorSeguroDeVida
	}

	var factorSeguroDeDanos = () => {
		var tipoDePagoSeleccionado;
		if (state.tipoDePago == 0) tipoDePagoSeleccionado = "pagoFijo";
		if (state.tipoDePago == 1)
			tipoDePagoSeleccionado = "pagoCreciente";
		var dosMillones;
		if (store.getters._montoDeCredito >= 2000000) dosMillones = "mayorDosMillones";
		if (store.getters._montoDeCredito < 2000000) dosMillones = "menorDosMillones";
		var ochenta;
		if ((porcentajeDeCredito() * 100) >= 80) ochenta = "mayorOchenta";
		if ((porcentajeDeCredito() * 100) < 80) ochenta = "menorOchenta";

		return productoAsignado()[tipoDePagoSeleccionado][dosMillones][
			ochenta
		]["seguroDanos"];
	}




	var interes = () => {
		return (store.getters._montoDeCredito * tasa()) / 12;
	}

	var amortizacion = () => {
		return interes() * factorAmortizacion();
	}

	var seguroDeVida = () => {
		return store.getters._montoDeCredito * factorSeguroDeVida();
	}

	var seguroDeDanos = () => {
		return Math.max(state.valorDelInmueble * 0.8, store.getters._montoDeCredito) *
			factorSeguroDeDanos();
	}

	var pagoMensual = () => {
		return parseFloat((interes() + amortizacion()).toFixed(2)) + seguroDeDanos() + seguroDeVida();
	}

	var ingresosRequeridos = () => {
		if (state.tipoDeIngresoSeleccionado == "Asalariado") {
			return pagoMensual() * 2;
		} else {
			return pagoMensual() * 2.78;
		}
	}

	var creditoInicial = () => {
		if (state.tipoDeIngresoSeleccionado == "Asalariado" && (state.tipoDeApoyoSeleccionado == "cofinavit" || state.tipoDeApoyoSeleccionado == "infonavit")) {
			return state.valorDelInmueble * .90;
		} else if (state.tipoDeIngresoSeleccionado == "Asalariado") {
			return state.valorDelInmueble * .85;
		} else {
			return state.valorDelInmueble * .80;
		}
	}
	var disponibilidad = () => {

		var credito = false;
		// console.log(store.getters._montoDeCredito, creditoInicial());

		if (store.getters._montoDeCredito <= creditoInicial()) {
			credito = true
		}
		// console.log("credito:", credito)
		var pago = state.tipoDePago;
		var plazo = false;
		if (pago == 0) {
			plazo = productoAsignado().plazosFijos[state.plazoSeleccionado]
		} else if (pago == 1) {
			plazo = productoAsignado().plazosCrecientes[state.plazoSeleccionado]
		}
		// console.log("plazo", plazo)

		// console.log("credito:", credito)
		// console.log(credito);
		// console.log(plazo, credito);
		if (plazo && credito) {
			return true
		} else {
			return false
		}
	}

	return {

		nombre: data.nombre,
		interes: interes(),
		amortizacion: amortizacion(),
		seguroDeVida: seguroDeVida(),
		seguroDeDanos: seguroDeDanos(),
		pagoMensual: pagoMensual(),
		ingresosRequeridos: ingresosRequeridos(),
		tasa: tasa(),
		creditoInicial: creditoInicial(),
		porcentajeDeCredito: porcentajeDeCredito(),
		disponibilidad: disponibilidad()
	}

}