import store from "../store";

export default (state) => {

    const data = {
        nombre: "afirme",
        factorSeguroDeVida: 0.0008,
        iva: 0,

        productos: [{
            nombre: "Comprar mi Casa",
            porcentajeMaximoDeCredito: 0.9,
            plazosFijos: [true, true, true],
            plazosCrecientes: [false, true, true],
            factoresAmortizacionFijos: [0.4929838883, 0.2334665989, 0.1245169539],
            factoresAmortizacionCrecientes: [0, 0.0807445358, 0.040091906],
            tasa: [0.1099, 0.1099, 0.1099],
            factorSeguroDeDanos: 0.000069313,
            factorSeguroDesempleoContenidos: 0.0001249,
            comisionAdministracion: 299,
        }]
    };

    var productoAsignado = () => {
        return data.productos.filter(producto => {
            return producto.nombre == state.productoSeleccionado;
        })[0]; // devolver el primer objeto para no obtener un array
    }

    var porcentajeDeCredito = () => {
        return (
            (store.getters._montoDeCredito * 100) /
            state.valorDelInmueble /
            100
        ) || 0;
    }

    var tasa = () => {
        return productoAsignado().tasa[state.plazoSeleccionado];
    }

    var factorSeguroDeDanos = () => {
        return productoAsignado().factorSeguroDeDanos;
    }

    var factorSeguroDesempleoContenidos = () => {
        return productoAsignado().factorSeguroDesempleoContenidos
    }

    var factorSeguroDeVida = () => {
        return data.factorSeguroDeVida
    }

    var comisionAdministracion = () => {
        return productoAsignado().comisionAdministracion
    }

    var factorAmortizacion = () => {
        var tipoDeFactores;
        if (state.tipoDePago == 0) tipoDeFactores = "factoresAmortizacionFijos";
        if (state.tipoDePago == 1)
            tipoDeFactores = "factoresAmortizacionCrecientes";
        return productoAsignado()[tipoDeFactores][state.plazoSeleccionado];
    }


    var interes = () => {
        return (store.getters._montoDeCredito * tasa()) / 12;
    }

    var amortizacion = () => {
        return interes() * factorAmortizacion();
    }


    var seguroDeVida = () => {
        return store.getters._montoDeCredito * factorSeguroDeVida();
    }

    var seguroDesempleoContenidos = () => {
        return store.getters._montoDeCredito * factorSeguroDesempleoContenidos();
    }

    var seguroDeDanos = () => {
        return state.valorDelInmueble * factorSeguroDeDanos();
    }

    var iva = () => {
        return (seguroDeDanos() + seguroDesempleoContenidos() + comisionAdministracion()) * 0.16
    }

    var pagoMensual = () => {
        return parseFloat((interes() + amortizacion()).toFixed(2)) + seguroDeDanos() + seguroDeVida() + seguroDesempleoContenidos() + comisionAdministracion() + iva();
    }

    var ingresosRequeridos = () => {
        if (state.tipoDeIngresoSeleccionado == "Asalariado") {
            return pagoMensual() * 2;
        } else {
            return pagoMensual() * 2.78;
        }
    }

    var creditoInicial = () => {
        if (state.tipoDeIngresoSeleccionado == "Asalariado" && (state.tipoDeApoyoSeleccionado == "cofinavit" || state.tipoDeApoyoSeleccionado == "infonavit")) {
            return state.valorDelInmueble * .95;
        } else if (state.tipoDeIngresoSeleccionado == "Asalariado") {
            return state.valorDelInmueble * .90;
        } else {
            return state.valorDelInmueble * .90;
        }
    }
    var disponibilidad = () => {

        var credito = false;
        // console.log(store.getters._montoDeCredito, creditoInicial());

        if (store.getters._montoDeCredito <= creditoInicial()) {
            credito = true
        }
        // console.log("credito:", credito)
        var pago = state.tipoDePago;
        var plazo = false;
        if (pago == 0) {
            plazo = productoAsignado().plazosFijos[state.plazoSeleccionado]
        } else if (pago == 1) {
            plazo = productoAsignado().plazosCrecientes[state.plazoSeleccionado]
        }
        // console.log("plazo", plazo)

        // console.log("credito:", credito)
        // console.log(credito);
        // console.log(plazo, credito);

        // console.log(plazo, credito);
        // console.log(creditoInicial());



        if (plazo && credito) {
            return true
        } else {
            return false
        }
    }
    return {
        nombre: data.nombre,
        interes: interes(),
        amortizacion: amortizacion(),
        seguroDeVida: seguroDeVida(),
        seguroDeDanos: seguroDeDanos(),
        pagoMensual: pagoMensual(),
        ingresosRequeridos: ingresosRequeridos(),
        tasa: tasa(),
        creditoInicial: creditoInicial(),
        porcentajeDeCredito: porcentajeDeCredito(),
        disponibilidad: disponibilidad(),
        seguroDesempleoContenidos: seguroDesempleoContenidos()
    }
}