import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/store'

Vue.config.productionTip = false

// muse ui components

import 'muse-ui/lib/styles/base.less';
import 'muse-ui/dist/muse-ui.css';
import {
  TextField,
  Select,
  Radio,
  Dialog,
  Tooltip,
  Checkbox
} from 'muse-ui';
import 'muse-ui/lib/styles/theme.less';

Vue.use(TextField)
Vue.use(Select)
Vue.use(Radio)
Vue.use(Dialog)
Vue.use(Tooltip)
Vue.use(Checkbox)

import theme from 'muse-ui/lib/theme';
theme.add('teal', {
  primary: '#00bcd4',
  secondary: '#009292',
  success: '#4caf50',
  warning: '#ffeb3b',
}, 'light');

theme.use('teal');

// currency filter


import VueCurrencyFilter from 'vue-currency-filter'

Vue.use(VueCurrencyFilter, {
  symbol: '$',
  thousandsSeparator: ',',
  fractionCount: 2,
  fractionSeparator: '.',
  symbolPosition: 'front',
  symbolSpacing: true
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')