import banamex from "../bancos/banamex"
import scotiabank from "../bancos/scotiabank"
import hsbc from "../bancos/hsbc"
import banorte from "../bancos/banorte"
import santander from "../bancos/santander"
import banregio from "../bancos/banregio"
import bxmas from "../bancos/bxmas"
import afirme from "../bancos/afirme"

export default {

    banamex(state) {
        return banamex(state)
    },

    scotiabank(state) {
        return scotiabank(state)
    },

    hsbc(state) {
        return hsbc(state)
    },

    banorte(state) {
        return banorte(state)
    },

    santander(state) {
        return santander(state)
    },

    banregio(state) {
        return banregio(state)
    },

    bxmas(state) {
        return bxmas(state)
    },

    afirme(state) {
        return afirme(state)
    },

    creditoPreAutorizado(state) {
        var capacidadInicial = 0;
        if (state.tipoDeIngresoSeleccionado == "Asalariado") {
            capacidadInicial = (state.ingresoMensual / 2)
        } else {
            capacidadInicial = (state.ingresoMensual * .36)
        }

        if (state.tipoDeBuro == 1) {

            if (state.pagosDeBuroActivos.pagosAutomotriz) {
                capacidadInicial = capacidadInicial - state.pagosDeBuro.pagosAutomotriz;
            }

            if (state.pagosDeBuroActivos.pagosHipotecario) {
                capacidadInicial = capacidadInicial - state.pagosDeBuro.pagosHipotecario;
            }

            return capacidadInicial * 100

        } else if (state.tipoDeBuro == 2) {

            capacidadInicial = capacidadInicial - state.pagosDeBuro.pagosMinimos;

            if (state.pagosDeBuroActivos.pagosAutomotriz) {
                capacidadInicial = capacidadInicial - state.pagosDeBuro.pagosAutomotriz;
            }

            if (state.pagosDeBuroActivos.pagosHipotecario) {
                capacidadInicial = capacidadInicial - state.pagosDeBuro.pagosHipotecario;
            }

            return capacidadInicial * 100

        } else {
            return capacidadInicial * 100
        }


    },

    _montoDeCredito(state) {
        if (state.tipoDeIngresoSeleccionado == "Asalariado") {
            if (state.tipoDeApoyoSeleccionado == "cofinavit") {

                if (state.montoDeApoyo >= (state.valorDelInmueble * .15)) {
                    return state.montoDeCredito || (state.valorDelInmueble * 1.1) - state.montoDeApoyo;
                } else if (state.montoDeApoyo < (state.valorDelInmueble * .15) && state.montoDeApoyo > (state.valorDelInmueble * .05)) {
                    return state.montoDeCredito || state.valorDelInmueble - state.montoDeApoyo
                } else if (state.montoDeApoyo <= (state.valorDelInmueble * .05)) {
                    return state.montoDeCredito || state.valorDelInmueble * .95;
                } else {
                    return state.montoDeCredito || state.valorDelInmueble * .95;
                }

            } else if (state.tipoDeApoyoSeleccionado == "infonavit") {

                return state.montoDeCredito || state.valorDelInmueble * .95;

            } else {

                // if (state.tipoDeIngresoSeleccionado == "Asalariado") {
                //     return state.montoDeCredito || state.valorDelInmueble * .90;
                // } else {
                return state.montoDeCredito || state.valorDelInmueble * .90;
                // }

                // return state.montoDeCredito;
            }
        } else {
            return state.montoDeCredito || state.valorDelInmueble * .90;
        }
    },
    montoDeCreditoConEscrituras(state) {
        console.log(state.montoDeApoyo, state.valorDelInmueble * .15)
        if (state.montoDeApoyo >= (state.valorDelInmueble * .15) && state.valorDelInmueble) {
            return true
        } else {
            return false
        }
    },
    banamex(state) {
        return banamex(state)
    }
}