import store from "../store";


export default (state) => {
	const data = {
		nombre: "banregio",
		factorSeguroDeVida: 0.00053,
		iva: 0,

		productos: [{
			nombre: "Comprar mi Casa",
			porcentajeMaximoDeCredito: 0.9,
			plazosFijos: [true, true, true],
			plazosCrecientes: [false, false, true],
			factorSeguroDesempleo: 0.0003,
			datosVariables: {
				asalariado: {
					fijos: {
						mayor: {
							factor: [0.474339878, 0.199229681, 0.088290777],
							tasa: [0.11, 0.115, 0.12]
						},
						menor: {
							factor: [0.531857836, 0.228884299, 0.1043032],
							tasa: [0.1025, 0.1075, 0.1125]
						}
					},
					crecientes: {
						mayor: {
							factor: [0, 0, 0.018818555],
							tasa: [0, 0, 0.12]
						},
						menor: {
							factor: [0, 0, 0.0331296],
							tasa: [0, 0, 0.1125]
						}
					}
				},
				otros: {
					fijos: {
						mayor: {
							factor: [0.484450424, 0.206189449, 0.092741888],
							tasa: [0.11, 0.115, 0.12]
						},
						menor: {
							factor: [0.543088698, 0.236784012, 0.109492114],
							tasa: [0.1025, 0.1075, 0.1125]
						}
					},
					crecientes: {
						mayor: {
							factor: [0, 0, 0.018818555],
							tasa: [0, 0, 0.12]
						},
						menor: {
							factor: [0, 0, 0.0331296],
							tasa: [0, 0, 0.1125]
						}
					}
				}
			},
			comisionAdministracion: 250
		}]
	};

	var productoAsignado = () => {
		return data.productos.filter(producto => {
			return producto.nombre == state.productoSeleccionado;
		})[0]; // devolver el primer objeto para no obtener un array
	}

	var porcentajeDeCredito = () => {

		return (
			(store.getters._montoDeCredito * 100) /
			state.valorDelInmueble /
			100
		) || 0;

	}

	var tasa = () => {
		var tipoDeFactores;
		var tipoDeIngresos;
		var porcentaje;

		if (state.tipoDeIngresoSeleccionado == "Asalariado") tipoDeIngresos = "asalariado";
		if (state.tipoDeIngresoSeleccionado != "Asalariado") tipoDeIngresos = "otros";

		if (state.tipoDePago == 0) tipoDeFactores = "fijos";
		if (state.tipoDePago == 1) tipoDeFactores = "crecientes";

		if (store.getters._montoDeCredito > state.valorDelInmueble * 0.7)
			porcentaje = "mayor";
		if (store.getters._montoDeCredito <= state.valorDelInmueble * 0.7)
			porcentaje = "menor";

		return productoAsignado().datosVariables[tipoDeIngresos][
			tipoDeFactores
		][porcentaje].tasa[state.plazoSeleccionado];
	}

	var factorAmortizacion = () => {
		var tipoDeFactores;
		var tipoDeIngresos;
		var porcentaje;

		if (state.tipoDeIngresoSeleccionado == "Asalariado") tipoDeIngresos = "asalariado";
		if (state.tipoDeIngresoSeleccionado != "Asalariado") tipoDeIngresos = "otros";

		if (state.tipoDePago == 0) tipoDeFactores = "fijos";
		if (state.tipoDePago == 1) tipoDeFactores = "crecientes";

		if (store.getters._montoDeCredito > state.valorDelInmueble * 0.7)
			porcentaje = "mayor";
		if (store.getters._montoDeCredito <= state.valorDelInmueble * 0.7)
			porcentaje = "menor";

		return productoAsignado().datosVariables[tipoDeIngresos][
			tipoDeFactores
		][porcentaje].factor[state.plazoSeleccionado];
	}


	var factorSeguroDeVida = () => {
		return data.factorSeguroDeVida
	}

	var factorSeguroDesempleo = () => {
		return productoAsignado().factorSeguroDesempleo
	}

	var interes = () => {
		return (store.getters._montoDeCredito * tasa()) / 12;
	}

	var amortizacion = () => {
		return interes() * factorAmortizacion();
	}

	var seguroDeVida = () => {
		return store.getters._montoDeCredito * factorSeguroDeVida();
	}

	var seguroDeDanos = () => {
		var valorCasa = state.valorDelInmueble * 0.812;
		return valorCasa * 0.000074364;
	}

	var comisionAdministracion = () => {
		return productoAsignado().comisionAdministracion;
	}

	var seguroDeDesempleo = () => {
		if (state.tipoDeIngresoSeleccionado == "Asalariado") {
			return store.getters._montoDeCredito * factorSeguroDesempleo();
		} else {
			return 0;
		}
	}

	var pagoMensual = () => {
		return parseFloat((interes() + amortizacion()).toFixed(2)) + seguroDeDanos() + seguroDeVida() + comisionAdministracion() + seguroDeDesempleo();
	}

	var ingresosRequeridos = () => {
		if (state.tipoDeIngresoSeleccionado == "Asalariado") {
			return pagoMensual() * 2;
		} else {
			return pagoMensual() * 2.78;
		}
	}

	var creditoInicial = () => {
		if (state.tipoDeIngresoSeleccionado == "Asalariado" && (state.tipoDeApoyoSeleccionado == "cofinavit" || state.tipoDeApoyoSeleccionado == "infonavit")) {
			return state.valorDelInmueble * .95;
		} else if (state.tipoDeIngresoSeleccionado == "Asalariado") {
			return state.valorDelInmueble * .90;
		} else {
			return state.valorDelInmueble * .90;
		}
	}
	var disponibilidad = () => {

		var credito = false;
		// console.log(store.getters._montoDeCredito, creditoInicial());

		if (store.getters._montoDeCredito <= creditoInicial()) {
			credito = true
		}
		// console.log("credito:", credito)
		var pago = state.tipoDePago;
		var plazo = false;
		if (pago == 0) {
			plazo = productoAsignado().plazosFijos[state.plazoSeleccionado]
		} else if (pago == 1) {
			plazo = productoAsignado().plazosCrecientes[state.plazoSeleccionado]
		}
		// console.log("plazo", plazo)

		// console.log("credito:", credito)
		// console.log(credito);
		// console.log(plazo, credito);
		if (plazo && credito) {
			return true
		} else {
			return false
		}
	}
	return {
		nombre: data.nombre,
		interes: interes(),
		amortizacion: amortizacion(),
		seguroDeVida: seguroDeVida(),
		seguroDeDanos: seguroDeDanos(),
		pagoMensual: pagoMensual(),
		ingresosRequeridos: ingresosRequeridos(),
		tasa: tasa(),
		creditoInicial: creditoInicial(),
		porcentajeDeCredito: porcentajeDeCredito(),
		disponibilidad: disponibilidad()
	}

}